import React, { useContext } from "react";
import Footer from "../../components/footer/Footer";
import Header from "../../components/Header/Header";
import "../PageObjects/PageObject.css";
import { useNavigate } from "react-router-dom";
import { Context } from "../../ContextFetch/context";
import { useParams } from "react-router-dom";

const PageObject = () => {
  const navigate = useNavigate();
  const { objects } = useContext(Context);
  const { id } = useParams();

  const paramsFind = objects.find((el) => {
    return el.id === +id;
  });
  return (
    <>
      <Header />
      <div className="objects">
        <div className="objects__container">
          <h5 className="objects__link">
             - <span></span>
          </h5>
          <div className="objects__params">
            <h1 className="objects__label">{paramsFind?.title}</h1>
            <p className="page__object__p">{paramsFind?.desc}</p>
            <div className="page__object__params__parent">
              <img
                src={paramsFind?.object_images[0].images}
                alt={paramsFind?.title}
              />
              <div className="small__params__images">
                {paramsFind?.object_images.map((el) => {
                  return <img src={el.images} alt="" />;
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="page__object__zagolovok">
        <div className="zagolovok">
          <div className="zagolovok__container">
            <h1 className="zagolovok__label"></h1>
            <p className="zagolovok__content">
                LR 1750     
              ,  ,  
                    . <br />{" "}
              <br />  750-     
               140-  , 105- 
               , 21-  ,
              -  400-   
               . <br /> <br />
                     
                  
                    .{" "}
              <br /> <br />  750-     
               ,    13 x 13 .
                     
               370 %.   LR 1750    
               ,  , 
                    
              . <br /> <br />  750-   
                 140-  , 105-
                , 21-  ,
              -  400-   
               . <br /> <br />  
                     
                   
                 . <br /> <br />  750- 
                   ,  
               13 x 13 .     
                  370 %.  LR 1750  
                 , 
              ,       
               . <br /> <br />
               750-      
              140-  , 105- 
               , 21-  ,
              -  400-   
               . <br /> <br />
                     
                  
                    .{" "}
              <br /> <br />  750-     
               ,    13 x 13 .
                     
               370 %. <br /> <br />
            </p>
            <div className="btn__back">
              <button
                onClick={() => navigate("/objects")}
                className="back__btn"
              >
                Back
              </button>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default PageObject;
