import React from "react";
import "../mainHero/Hero.css";
import { HashLink } from "react-router-hash-link";
import { useNavigate } from "react-router-dom";

const Hero = () => {
  const navigate = useNavigate()
  return (
    <>
      <div className="hero">
        <div className="hero__container">
          <div className="hero__parent">
            <div className="hero__left">
              <div className="hero__empty"></div>
              <br />
              <br />
              <h1>
                  <br /> Liebherr   
              </h1>
              <p>
                ,     LIEBHERR   
                   .    
              </p>
              <div className="hero__btns">
                <HashLink smooth to="#order_call">
                  <button className="hero__btn1"> </button>
                </HashLink>
                <button className="hero__btn2" onClick={() => navigate("/catalog")}> </button>
              </div>
            </div>
            <div className="hero__right">
              <h1>{"01/04"}</h1>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Hero;
